.layout {
  min-height: 100vh;
  width: 100vw;
  font: normal 300 16px/1.5 $main-font;
}

.container {
  max-width: 1160px;
  margin: 0 auto;

  @media (max-width: 1365px) {
    margin: 0 45px;
  }

  @media (max-width: 720px) {
    margin: 0 15px;
  }
}

.desktop-only {
  @media (min-width: 1366px) {
    display: none;
  }
}
